import arrow from "../../images/arrow-7.svg"
import "./experience.css"

function ExpCard({ exp }) {
    const descComponents = exp.description.map((x, idx) => <p key={idx}>{x}</p>)

    return (
        <>
            <h3 className="experience-title">As a {exp.title}</h3>
            <h4 className="experience-subtitle">{exp.companyName} (From {exp.startDate} to {exp.endDate})</h4>
            <div className="experience-main">{descComponents}</div>
        </>
    )
}

export default function Experience() {

    const exps = [
        {
            companyName: "Reliance Industries Limited",
            title: "Backend Developer",
            startDate: "June 2023",
            endDate: "July 2023",
            description: [
                "Designed and implemented an API using Node.js and Express.js to store server logs in a MongoDB database.",
                "Gained proficiency in Node.js, Express.js, and MongoDB through self-directed learning, which was essential for project success."
            ]
        },
        {
            companyName: "Rapido Tasker",
            title: "Freelance App Developer",
            startDate: "July 2022",
            endDate: "October 2022",
            description: [
                "Developed Urban Company client-side and partner-side app clones for the company.",
                "Implemented essential features: Geo-location tracking with Realtime Firebase and Geofire, Google Maps integration for address, registration, Stripe payment gateway, and reward referrals using Firebase Dynamic links.",
                "Handled user authentication, service booking, in-app chat messaging, and app notifications.",
                "Utilized Flutter framework for hybrid app development and Firebase for the backend."
            ]
        }
    ]

    return (
        <div className="experience">
            <img className="experience-arrow" src={arrow} alt='arrow' />
            <h2 className="experience-header">Work Experience</h2>
            <ExpCard exp={exps[0]} />
            <ExpCard exp={exps[1]} />
        </div>
    )
}